import React, { Component } from 'react'
import {Badge,Avatar,Tabs,Modal,Button} from 'antd'
import {saveTagstoStorage,getActiveTagKey,getTagsArr} from '../../utils/storageUtils'
import {Link,withRouter} from 'react-router-dom'
import { UserOutlined,CloseCircleOutlined,ExclamationCircleOutlined } from '@ant-design/icons'
import PubSub from 'pubsub-js'
import './index.css'
import { reqGetWeather } from '../../api'
import {connect} from 'react-redux'
import {logout} from '../../redux/actions/user'
// 
const { confirm } = Modal;



// 

const {TabPane } = Tabs
class MyHeader extends Component {
    newTabIndex = 0;
    constructor(props){
        super(props)
        // tabs
        // home 
        let initialPanes = [];
        let activeKey = '';
        if (getTagsArr().length>0) {
            // tags
            initialPanes = getTagsArr();
            activeKey = getActiveTagKey();
        }else{
            //  none
            // console.log('',this.props.location)
            this.props.history.push('/home')
            initialPanes = [
                {   
                    title: '',    
                    key: '/home' 
                },
            ];
            activeKey = '/home';
            
        }

        this.state = {
            activeKey,
            panes: initialPanes,
            weather:{},
            time:''
        };
    }
    

    // tagsactive
    onChange = activeKey => {
        saveTagstoStorage(activeKey,this.state.panes)
        this.setState({ activeKey });
    };
    
    onEdit = (targetKey, action) => {
        this[action](targetKey);
    };
    
    add = (paneObj) => {
        const { panes } = this.state;
        const activeKey = paneObj.key;
        const newPanes = [...panes];
        console.log(paneObj,)
        //pane activeKye
        function getKey(pane){
            return pane.key === paneObj.key
        }
        if(!newPanes.find(getKey)){
            //pane
            newPanes.push(paneObj);//
        }
        //tagslocalstorage
        saveTagstoStorage(activeKey,newPanes)
        // 
        this.setState({
            panes: newPanes,
            activeKey,
        });
    };
    
    remove = targetKey => {
        const { panes, activeKey } = this.state;
        let newActiveKey = activeKey;
        let lastIndex;
        panes.forEach((pane, i) => {
            if (pane.key === targetKey) {
            lastIndex = i - 1;
            }
        });
        const newPanes = panes.filter(pane => pane.key !== targetKey);
        if (newPanes.length && newActiveKey === targetKey) {
            if (lastIndex >= 0) {
                newActiveKey = newPanes[lastIndex].key;
                // active
            } else {
                // 
                newActiveKey = newPanes[0].key;
            }
            // activeKey
            this.props.history.replace(newActiveKey)

        }else if(newPanes.length === 0){
            // 
            console.log('meiyou biaoqianl')
            newActiveKey = '';
            this.props.history.replace('/none')
            
        }
        //tagslocalstorage
        saveTagstoStorage(newActiveKey,newPanes)
        this.setState({
            panes: newPanes,
            activeKey: newActiveKey,
        });
    };

    
// 
    logOut = () => {
        confirm({
            title: '?',
            icon: <ExclamationCircleOutlined />,
            onOk:()=> {
            //   

            // 
            // memoryUtils.user ={}

            // reduxuser
            this.props.logout();
            
            // Login
                this.props.history.replace('/login')
            },
        });
    }


     // 
    componentDidMount(){
        // sider
        PubSub.subscribe('addTags',(msg,data) => {
            // console.log('sider')
            // console.log(data)
            this.add(data)  //{key:'/home',title:''}
        })
        // 
        let weather = {}
        let getWeather = async ()=>{
            weather = await reqGetWeather({
                key:'5054ae320e60bfdf4c03a21cb2efec52',
                city:'110101',
                extensions:'all',
                output:'json'
            })
            this.setState({weather})
            // console.log('')
        }
        getWeather()

        // 
        this.timer = setInterval(() => {
            let time = new Date().toLocaleTimeString();
            this.setState({time})
        }, 1000);
    }
   
    componentWillUnmount(){
        clearInterval(this.timer)
    }

    render () {
        
        // 
        const { panes, activeKey,weather,time } = this.state;
        const {user} = this.props;
        let  dayWeather =''
        if (weather.forecasts) {
            dayWeather= weather.forecasts[0].casts[0].dayweather
        }
        // console.log(this.props)
        
        return (
            <div className = 'admin-header'>
                {/*  */}
                <div className="header-top">
                   
                     {/*  */}
                     <span className="avatar-item">
                        <Badge size='small' count={99}>
                            <Avatar  shape="square" icon={<UserOutlined />} />
                        </Badge>
                    </span>
                    
                    {/*  */}
                    <span className='header-username'>{user.username}</span>
                    {/* out */}
                    <span className = 'quite-login'>
                        <Button type="link" onClick = {this.logOut}></Button>
                    </span>
                
                </div>
                {/*  */}
                <div className="header-bottom">
                    {/* tabs  */}
                    <div className="header-bottom-left">
                        <Tabs  hideAdd type="editable-card" size='small'
                            onChange={this.onChange}
                            activeKey={activeKey}
                            onEdit={this.onEdit}
                            
                        >
                            {panes.map(pane => (
                                    <TabPane  tab={
                                        // pane.title
                                        <Link to={pane.key}>{pane.title}</Link>
                                    } key={pane.key} closable={pane.closable} closeIcon ={<CloseCircleOutlined /> } />    
                            ))}
                        </Tabs>
                    </div>
                                {/* */}
                    <div className="header-bottom-right">
                        <div className = 'timeText'>
                            {new Date().toLocaleDateString()}
                            <br/>
                            {time}
                        </div>
                        <span className='wether'></span>
                        {dayWeather}
                    </div>
                </div>
            </div>
        )
    }
}

export default connect(
    state=>({user:state.user}),
    {
        logout
    }
)(withRouter(MyHeader))