import React, { Component } from 'react'
import {Modal,Form, Input,Select, message, Button} from 'antd'
import {reqAddOrUpdateUser} from '../../api'
const {Item} = Form;
const {Option} = Select
export default class AddUpdateModel extends Component {
    
    state = {
        visible:false,
        userdata :{},
        rolesArr : []
    }
    handleOk =async (e) => {
        let _id = this.state.userdata._id;
        // 
        let value = this.form.getFieldValue();

        value =_id? {...value,_id:this.props.userdata._id}:value
        // 
        this.handleAddAndUpdateUser(value)
        // this.props.userdata._id?this.handleUpdateUser(value):this.handleAddUser(value)
        
        // 
        this.handleCancel()

    };
    handleCancel = () => {
        // message.success('')
        this.props.changeModelStatus(0)
    };
    onFinish = (values) => {
        console.log('Success:', values);
    };
    onFinishFailed = (values) => {
        console.log('error:', values);
    }
    // 
    // handleChange = (value)=>{
    //     console.log(`selected ${value}`);
    // }
    
    handleAddAndUpdateUser =async (data) => {
        let str = this.props.userdata._id?'':''
        let result = await reqAddOrUpdateUser(data);
        if (result.status === 0) {
            message.success(str+'')
            // 
            this.props.getUserList();
        }else{
            message.error(result.msg)
        }
    }
    
    
    static getDerivedStateFromProps(props,state){
        // console.log(props,state)
        const {viewModleStaus,userdata,rolesData} = props
        // render  category
        if (viewModleStaus===1) {
            return {
                visible:true,
                userdata,
            }
        }else{
            return {visible:false}
        }
    }
    
    componentDidMount(){
        // console.log('DidMount',this.form)
        // this.form&&this.form.setFieldsValue(()=>({ parentCaregory:parentId  }))
    }
    componentWillUnmount(){
        console.log('will mount')
    }
    
    
    render() {
        const {visible} = this.state
        const {parentId,rolesArr,userdata} = this.props
        const {email,password,phone,username,role_id,_id} = userdata;
        const initialValues = {
            username,
            email,
            password,
            phone,
            role_id
        }
        return (
            <Modal
                title={_id?'':''}
                visible={visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
            >
                {/*  */}
                <Form ref = {c=>this.form = c}  name="basic" labelCol={{ pan: 8, }}
                    wrapperCol={{ span: 16, }}
                    initialValues={initialValues}
                    onFinish={this.onFinish}
                    onFinishFailed = {this.onFinishFailed}
                    labelCol={{span:6}}
                    wrapperCol={{span:12}}
                    validateTrigger={['onBlur','']}
                >
                     <Item  label="" name="username" placeholder=''
                        rules={[
                                {
                                    required: true,
                                    message: '',
                                },
                                
                                {
                                    type:'string',
                                    min:5,
                                    message:'5'
                                },
                                {
                                    max:20,
                                    message:'20'
                                },
                                {
                                    pattern:/^[\w]+$/,
                                    message:''
                                }
                            ]}
                        >
                        <Input />
                    </Item>
                    {
                        _id?'':(
                            <Item  label="" name="password"  placeholder=''
                                rules={[
                                        {
                                            required: true,
                                            message: '',
                                        },
                                        {
                                            min:6,
                                            message:'6'
                                        },
                                        {
                                            pattern:/^[\w]+$/,
                                            message:''
                                        }

                                    ]}
                                >
                                <Input type ='password' />
                            </Item>
                        )
                    }


                     <Item  label="" name="phone" placeholder=''
                        rules={[
                                {
                                    required: true,
                                    message: '',
                                },
                                {
                                    pattern:/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/,
                                    message:''
                                }
                            ]}
                        >
                        <Input type='text' />
                    </Item>
                     <Item  label="" name="email" placeholder=''
                        rules={[
                                {
                                    required: true,
                                    message: '',
                                },
                                {
                                    pattern:/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
                                    message:''
                                }
                            ]}
                        >
                        <Input type = 'email'  />
                    </Item>
                


                    <Item label='' name='role_id'
                        rules={[
                            
                        ]}
                    >
                        <Select
                            showSearch
                            
                            placeholder=""
                            optionFilterProp="children"
                            onChange={this.onChange}
                            
                            onSearch={this.onSearch}
                            value ={parentId}
                            // defaultValue= {parentId||''}
                            filterOption={(input, option) =>
                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                // console.log(option)
                            }
                        >
                            
                            {rolesArr.map((data) => {
                                return (
                                    <Option  value={data._id} key = {data._id}>{data.name}</Option>
                                )
                            })}
                            
                        </Select>
                    </Item>
                    <Button ref={(e)=>this.submitBtnRef = e} type='primary' htmlType='submit'></Button>
                </Form>
            </Modal>
        )
    }
}



  
