import React,{Component} from 'react'
import { Upload, Modal, message } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import {reqDeleteImg} from '../../../../api'
function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

export default class PicturesWall extends Component {
    constructor(props){
        super(props);
        let fileList = [];
        if(this.props.isUpdate){
            fileList = this.props.images.map((img,index) => {
                return {
                    uid:'-'+index,
                    percent: 50,
                    name: img,
                    status: 'done',
                    url: '/api1//upload/'+img,//
                  }
            })
        }
        this.state = {
            previewVisible: false,//
            previewImage: '',//
            previewTitle: '',//
            fileList,
        
        };
    }
  

//   getFileList = () => {
      
//   }

// form 
    imgsDataToFather = () => {
        const imgsData = this.state.fileList.map(file=>file.name);
        this.props.getImgDataToForm(imgsData);
    }

  deleteImg =async (name) => {
      const result = await reqDeleteImg(name);

      if (name === ''||result.status ===0) {
          message.success('');
        //   jforn
        this.imgsDataToFather();
      }else{
          message.error('')
      }
  }
  handleCancel = () => this.setState({ previewVisible: false });



  handlePreview = async file => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    this.setState({
      previewImage: file.url || file.preview,
      previewVisible: true,
      previewTitle: file.name || file.url.substring(file.url.lastIndexOf('/') + 1),
    });
  };

  handleChange = ({ fileList,file }) =>{
      console.log('change',fileList)
      console.log('change-file',file)

    //   
      if (file.status === 'done') {
          message.success('',1);
        // url
        const newfile = fileList[fileList.length-1];
        newfile.name = file.response.data.name;
        newfile.url = '/api1/upload/'+newfile.name;

        
      }
      if (file.status === "removed") {
        this.deleteImg(file.name);
      }
    //   
      this.setState({ fileList })

      // 
      this.imgsDataToFather();
  };

  render() {
    const { previewVisible, previewImage, fileList, previewTitle } = this.state;
    const uploadButton = (
      <div>
        <PlusOutlined />
        <div style={{ marginTop: 8 }}>Upload</div>
      </div>
    );
    return (
      <>
        <Upload
          accept ='image/*' //
          name	='image' //
          action="/api1/manage/img/upload" //
          listType="picture-card"   // text, picture  picture-card  
          fileList={fileList}   //
          onPreview={this.handlePreview} //
          onChange={this.handleChange}  // 
        >
            {/*//8  */}
          {fileList.length >= 8 ? null : uploadButton} 
        </Upload>

        {/*  */}
        <Modal
          visible={previewVisible}  //
          title={previewTitle}  //
          footer={null} //
          onCancel={this.handleCancel}
        >
          <img alt="example" style={{ width: '100%' }} src={previewImage} />
        </Modal>
      </>
    );
  }
}