/**
 *  ----- 
 */
import React, { Component } from 'react'
import {Modal,Form, Input, message,Tree} from 'antd'
import {reqUpdateRole} from '../../../api'
import {withRouter} from 'react-router-dom'
import menuConfig from '../../../config/menuConfig'
import {connect} from 'react-redux'
import {logout} from '../../../redux/actions/user'
const {Item} = Form;

const treeData = menuConfig
 class ConfigRole extends Component {
    constructor(props){
        super(props)
        let {menus} = this.props.role
        // console.log(menus)
        this.state = {
            visible:false,
            menus,
            user:this.props.user
        }
    }
    

    onSelect = (selectedKeys, info) => {
        // console.log('selected', selectedKeys, info);
    };
    
    onCheck = (checkedKeys, info) => {
        // console.log('onCheck', checkedKeys, info);
        this.setState({menus:checkedKeys});
    };


    // 
    handleOk =async (e) => {
        const {_id} = this.props.role;
        const {menus} = this.state;
        const {username} = this.props.user
        let auth_time = new Date();
        // 
        let result =await reqUpdateRole({_id,menus,auth_name:username,auth_time})
        if (result.status === 0) {
            
            
            // 
            this.handleCancel()
            if(this.props.user.role._id !== _id){
                // 
                message.success('')
                this.props.getRoles();
                // return;
            }else{
                 // 
                 message.warning('')
                 // 
                 this.props.logout();
                 this.props.history.replace('/login')
            }
            
            
            
        }else{
            message.error(result.msg)
        }
        

    };
    // 
    handleCancel = () => {
        // console.log('Clicked cancel button');
        this.props.changeModelStatus(0)
    };
    
   
    
    static getDerivedStateFromProps(props,state){
        // console.log(props,state)
        const {viewModleStaus} = props
        // render  category
        if (viewModleStaus===1) {
            return {
                visible:true,
            }
        }else{
            return {visible:false}
        }
    }
    
    componentDidMount(){
    }
    
    
    
    render() {
        const {visible,menus} = this.state
        const {role} = this.props
        return (
            <Modal
                title="Title"
                visible={visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
            >
                {/*  */}
                <Form ref = {c=>this.form = c}  name="basic" labelCol={{ pan: 8, }}
                    wrapperCol={{ span: 16, }}
                    initialValues={{ roleName: role.name}}
                    onFinish={this.onFinish}
                    >
                    
                    <Item  label="" name="roleName" 
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}
                    >
                        <Input disabled={true}/>
                    </Item>

                    <Tree
                        checkable
                        // defaultExpandedKeys={['0-0-0', '0-0-1']} //
                        // defaultSelectedKeys={['0-0-0', '0-0-1']} //
                        defaultCheckedKeys={menus} //
                        onSelect={this.onSelect}
                        onCheck={this.onCheck}
                        treeData={treeData}
                    />
                    
                </Form>
            
            </Modal>
        )
    }
}

export default  connect(
    state=>({user:state.user}),
    {
        logout
    }
)(withRouter(ConfigRole))


  
