import React, { Component } from 'react'
import {Modal,Form, Input,Select, message} from 'antd'
import {reqAddRole} from '../../../api'
const {Item} = Form;
const {Option} = Select
export default class AddRole extends Component {
    
    state = {
        visible:false,
        confirmLoading:false,
        modalText:'Content of the modal',
        optionsData : []
    }
    // 
    handleOk =async (e) => {
        const {roleName} = this.form.getFieldValue()
        if(roleName===undefined||roleName===''){
            console.log('')
            message.warning('')
            return
        }
        // console.log('roleName,',roleName)
        // 
        let result =await reqAddRole(roleName)
        if (result.status === 0) {
            message.success('')
            // 
            this.props.getRoles();

        }else{
            message.error('')
        }
        // 
        this.handleCancel()

    };
    // 
    handleCancel = () => {
        // console.log('Clicked cancel button');
        this.props.changeModelStatus(0)
    };
    
   
    
    static getDerivedStateFromProps(props,state){
        // console.log(props,state)
        const {viewModleStaus} = props
        // render  category
        if (viewModleStaus===2) {
            return {
                visible:true,
            }
        }else{
            return {visible:false}
        }
    }
    
    componentDidMount(){
    }
    
    
    
    render() {
        const {visible,} = this.state
        const {} = this.props
        return (
            <Modal
                title="Title"
                visible={visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
            >
                {/*  */}
                <Form ref = {c=>this.form = c}  name="basic" labelCol={{ pan: 8, }}
                    wrapperCol={{ span: 16, }}
                    initialValues={{  }}
                    onFinish={this.onFinish}
                    >
                    
                    <Item  label="" name="roleName" 
                        rules={[
                            {
                                required: true,
                                message: '',
                            },
                        ]}
                    >

                        <Input />
                    </Item>
                    
                </Form>
            </Modal>
        )
    }
}



  
